@keyframes ripple {
  0% {
    top: 36px;
    left: 36px;
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    top: 0;
    left: 0;
    width: 72px;
    height: 72px;
    opacity: 0;
  }
}

.preloader {
  flex-grow: 1;
  display: flex;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
}

.preloader__container {
  display: block;
  position: relative;
  width: 80px;
  height: 80px;
}

.preloader__block {
  position: absolute;
  border: 4px solid var(--colorUIPrimary);
  opacity: 1;
  border-radius: 50%;
  animation: ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}

.preloader__block:nth-child(2) {
  animation-delay: -0.5s;
}
